      saveDraft: function saveDraft() {
        var opt = this.get('model');
        //failsafe
        if (opt.get('company') === '' || opt.get('company') === null || opt.get('company') === undefined) {
          return;
        }
        opt.set('draft', true);
        opt.set('newRecord', false);
        this.doSave();
      },

